                value: ['chart11','-20',['电脑室','化学实验室','图书室','体育室'],[561,245,543,423]],
                label: '全部'
            }, {
                key:'2',
                value: ['chart11','0',['教室','化学室','书本','椅子'],[261,255,343,223]],
                label: '专业'
            }],
        }
    },
    methods: {
        handleClick(tab, event) {
            // console.log(tab, event);
        },
        getValue(value){
            console.log(value)
            var mychart = this.$echarts.init(document.getElementById(value[0]))
            this.baroption.xAxis[0].axisLabel.rotate = value[1]
            this.baroption.xAxis[0].data = value[2]
            this.baroption.series[0].data = value[3]
            mychart.clear();
            mychart.setOption(this.baroption,true)
        }
    },
    components: {
        barCharts,
    },
    mounted() {
        var _this = this
         _this.chart = true
        $.ajax({
            type: "post",
            url: _this.baseUrl+"/ydjxzg/hx/xsgl.action",
            data: {reg:'zyjxsx',style:'1'},
            dataType: "json",
            success: function (res) {
                console.log(res)
                if(res.sxmydpm){
                    _this.xData = []
                    _this.yData = []
                   // _this.baroption1.param.push(res.sxmydpm[0].param1)
                    for(var i = 1;i < res.sxmydpm.length;i++){
                        _this.xData.push(res.sxmydpm[i].mc)
                        _this.yData.push(res.sxmydpm[i].zj)
                    }
                    _this.baroption1.yAxis[0].name = '(分)'
                    _this.baroption1.xAxis[0].data = _this.xData;
                    _this.baroption1.series[0].data = _this.yData;
                }
                if(res.kyslpm){
                    _this.xData = []
                    _this.yData = []
                    //_this.baroption3.param.push(res.zyjylpm[0].param1)
                    for(var i = 1;i < res.kyslpm.length;i++){
                        _this.xData.push(res.kyslpm[i].mc)
                        _this.yData.push(res.kyslpm[i].zcount)
                    }
                    _this.baroption2.yAxis[0].name = '(个)'
                    _this.baroption2.xAxis[0].data = _this.xData;
                    _this.baroption2.series[0].data = _this.yData;
                }
                if(res.kyjepm){
                        _this.xData = []
                        _this.yData = []
                        //_this.baroption2.param.push(res.mylpm[0].param1)
                        for(var i = 1;i < res.kyjepm.length;i++){
                            _this.xData.push(res.kyjepm[i].mc)
                            _this.yData.push(res.kyjepm[i].zcount)
                        }
                        _this.baroption3.yAxis[0].name = '(万元)'
                        _this.baroption3.xAxis[0].data = _this.xData
                        _this.baroption3.series[0].data = _this.yData;
                        console.log(_this.baroption3)
                    }
                    if(res.sbjepm){
                        _this.xData = []
                        _this.yData = []
                        //_this.baroption2.param.push(res.mylpm[0].param1)
                        for(var i = 1;i < res.sbjepm.length;i++){
                            _this.xData.push(res.sbjepm[i].mc)
                            _this.yData.push(res.sbjepm[i].zj)
                        }
                        _this.baroption4.yAxis[0].name = '(万元)'
                        _this.baroption4.xAxis[0].data = _this.xData
                        _this.baroption4.series[0].data = _this.yData;
                        console.log(_this.baroption4)
                    }
                    if(res.sjgwpm){
                        _this.xData = []
                        _this.yData = []
                        //_this.baroption2.param.push(res.mylpm[0].param1)
                        for(var i = 0;i < res.sjgwpm[1].length;i++){
                            _this.xData.push(res.sjgwpm[1][i].mc)
                            _this.yData.push(res.sjgwpm[1][i].zcount)
                        }
                        _this.baroption5.yAxis[0].name = '(位)'
                        _this.baroption5.xAxis[0].data = _this.xData
                        _this.baroption5.series[0].data = _this.yData;
                        console.log(_this.baroption5)
                    }
                    if(res.xxsbzslpm){
                        _this.xData = []
                        _this.yData = []
                        //_this.baroption2.param.push(res.mylpm[0].param1)
                        for(var i = 1;i < res.xxsbzslpm.length;i++){
                            _this.xData.push(res.xxsbzslpm[i].mc)
                            _this.yData.push(res.xxsbzslpm[i].zcount)
                        }
                        _this.baroption6.yAxis[0].name = '(台)'
                        _this.baroption6.xAxis[0].data = _this.xData
                        _this.baroption6.series[0].data = _this.yData;
                        console.log(_this.baroption6)
                    }
                    if(res.sjslpm){
                        _this.xData = []
                        _this.yData = []
                        //_this.baroption2.param.push(res.mylpm[0].param1)
                        for(var i = 1;i < res.sjslpm.length;i++){
                            _this.xData.push(res.sjslpm[i].mc)
                            _this.yData.push(res.sjslpm[i].zcount)
                        }
                        _this.baroption7.yAxis[0].name = '(台)'
                        _this.baroption7.xAxis[0].data = _this.xData
                        _this.baroption7.series[0].data = _this.yData;
                        console.log(_this.baroption7)
                    }
                    if(res.xwjdslpm){
                        _this.xData = []
                        _this.yData = []
                        //_this.baroption2.param.push(res.mylpm[0].param1)
                        for(var i = 1;i < res.xwjdslpm.length;i++){
                            _this.xData.push(res.xwjdslpm[i].mc)
                            _this.yData.push(res.xwjdslpm[i].zcount)
                        }
                        _this.baroption8.yAxis[0].name = '(个)'
                        _this.baroption8.xAxis[0].data = _this.xData
                        _this.baroption8.series[0].data = _this.yData;
                        console.log(_this.baroption8)
                    }
                    if(res.hjqkpm){
                        _this.xData = []
                        _this.yData = []
                        //_this.baroption2.param.push(res.mylpm[0].param1)
                        for(var i = 1;i < res.hjqkpm.length;i++){
                            _this.xData.push(res.hjqkpm[i].mc)
                            _this.yData.push(res.hjqkpm[i].zcount)
                        }
                        _this.baroption9.yAxis[0].name = '(个)'
                        _this.baroption9.xAxis[0].data = _this.xData
                        _this.baroption9.series[0].data = _this.yData;
                        console.log(_this.baroption9)
                    }
                    if(res.xsxxmydpm){
                        _this.xData = []
                        _this.yData = []
                        //_this.baroption2.param.push(res.mylpm[0].param1)
                        for(var i = 1;i < res.xsxxmydpm.length;i++){
                            _this.xData.push(res.xsxxmydpm[i].mc)
                            _this.yData.push(Math.round(res.xsxxmydpm[i].zj))
                        }
                        _this.baroption10.yAxis[0].name = '(分)'
                        _this.baroption10.xAxis[0].data = _this.xData
                        _this.baroption10.series[0].data = _this.yData;
                        console.log(_this.baroption10)
                    }
                    if(res.gzyjftrpm){
                        _this.xData = []
                        _this.yData = []
                        //_this.baroption2.param.push(res.mylpm[0].param1)
                        for(var i = 1;i < res.gzyjftrpm.length;i++){
                            _this.xData.push(res.gzyjftrpm[i].mc)
                            _this.yData.push(res.gzyjftrpm[i].zcount)
                        }
                        _this.baroption11.yAxis[0].name = '(万元)'
                        _this.baroption11.xAxis[0].data = _this.xData
                        _this.baroption11.series[0].data = _this.yData;
                        console.log(_this.baroption11)
                    }
                _this.char = true
            },
            error:function(err){
                console.log(err)
            }
        });
    },
}
</script>

<style>
#jxsx{
    width: 100%;
    height: 100%;
}
#jxsx .row{
    display:flex;
    justify-content:center;
}
#jxsx .row .col{
    width:750px;
    margin-top:20px;
}
#jxsx .row .cardtitle{
    font-size:25px;
    text-align:center;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
}
#jxsx .row .coltips{
    min-width:280px;
    max-width:300px;
    margin-top:20px;
}
#jxsx .row .coltips2{
    min-width:150px;
    max-width:300px;
    margin-top:20px;
}
#jxsx .shaixuan_box{
    position:relative;
    left:13%;
    bottom:32px;
}
@media all and (max-width: 1200px) {
    #jxsx .shaixuan_box{
        left: 3%!important;
    }
}
#jxsx .row .tipheight{
    height:420px;
}
#jxsx .row .tip_select{
    height:452px;
}
#jxsx .row .tips{
    font-size:25px;
    text-align:left;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
    padding:0 20px;
}
#jxsx .row .tippoint{
    margin-top:50px;
    padding:0 20px;
    line-height:40px;
}
#jxsx .is-active{
    color: #029348 !important;
}
.el-tabs__active-bar{
    background: #029348 !important;
}
#jxsx .is-top :hover{
    color: #029348 !important;
}
#jxsx .is-left :hover{
    color: #029348 !important;
}
#jxsx .el-card{
    padding: 40px 0 40px 0;
}
.el-tabs__content{
    overflow:inherit;
    padding-bottom: 20px;
}
.el-card{
    transition: 0s;
}
</style>




